export default {
  name: 'Register',
  data() {
    return {
      email: '',
      showError: false,
    }
  },
  mounted() {
    this.register()
  },
  methods: {
    register() {
      // Confirm the link is a sign-in with email link.
      console.log(
        'emaillink: ',
        this.$fire.auth.isSignInWithEmailLink(window.location.href)
      )
      if (this.$fire.auth.isSignInWithEmailLink(window.location.href)) {
        const email = window.localStorage.getItem('emailForSignIn')
        if (email) {
          this.email = email
          this.login()
        }
      }
    },
    login() {
      // The client SDK will parse the code from the link for you.
      this.$fire.auth
        .signInWithEmailLink(this.email, window.location.href)
        .then((result) => {
          window.localStorage.removeItem('emailForSignIn')
          this.$router.push({
            path: '/registration/membership-selection',
            query: { eMail: this.email },
          })
        })
        .catch((e) => {
          console.log(e)
          this.showError = true
        })
    },
  },
}
